.media-library-item--grid .field--name-field-description {
  margin-left: 0.5rem;
  padding: 0.3125rem;
}

/* Overriding from docroot/core/themes/claro/css/theme/media-library.css for media widget display with more
 specificity to beat claro default. Keeping it wider allows lengthy strings to fit neatly in the card. */
@media screen and (min-width: 77em) {
  .media-library-widget-modal .media-library-item.media-library-item--grid,
  .media-library-selection .media-library-item.media-library-item--grid {
    width: 33.3%;
  }
}

// targeting lengthy PDF filenames with underscores
.file--application-pdf {
  word-break: break-word;
}

.downloadable-file-link {
  padding-left: 20px;
  position: relative;

  &::before {
    background-repeat: no-repeat;
    background-size: contain;
    content: '';
    display: block;
    height: 16px;
    position: absolute;
    top: 2px;
    width: 16px;
  }
}

.downloadable-file-link--document::before {
  background-image: url('../../images/icons/download-000000.svg');
}

.downloadable-file-link--video::before {
  background-image: url('../../images/icons/play-000000.svg');
}

.downloadable-file-link--image::before {
  background-image: url('../../images/icons/download-000000.svg');
}
